
<template>
  <div class="container">
    <div class="row">
      <div class="col-12 bio p-0" style="color: white; font-size: 5vh">
        {{ apiData.bio }}
      </div>
    </div>
    <div class="row mt-5">
      <a class="githubButton p-3" href="http://www.github.com/sedatates"
        >Send me to github 🚀</a
      >
    </div>
  </div>
</template>

<script >
import axios from "axios";
export default {
  data() {
    return {
      apiData: JSON,
    };
  },
  mounted() {
    axios
      .get("https://api.github.com/users/sedatates")
      .then((res) => (this.apiData = res.data))
      .catch((err) => console.log(err));
  },
};
</script>
<style>
/**/
.githubButton {
  border-radius: 5px;
  background-color: white;
  color: black;
  text-align: center;
}
</style>