<template>
  <div class="fixed-top">
    <header class="d-flex justify-content-between px-3 header">
      <div class="col-5 align-self-center ">
        <img src="../assets/logo.png " class="navbar-logo" />
      </div>
      <div class="navbar col-lg-3 justify-content-between">
        <div class="m-1"><a href="#">About</a></div>
        <div class="m-1"><a href="#Github">Github</a></div>
        <button class="mx-1 px-2 button_Header" v-on:click="openClose()">
          Contact
        </button>
      </div>
    </header>
    <div class="col-12 nopadding">
      <EmailForm :onoff="openCloseVariable" @sonuc="openClose()" />
    </div>
  </div>
</template>

<script>
import EmailForm from "./EmailForm.vue";
export default {
  data() {
    return {
      openCloseVariable: "visibility: hidden;",
    };
  },
  components: {
    EmailForm,
  },
  methods: {
    openClose() {
      if (this.openCloseVariable === "visibility: hidden;") {
        this.openCloseVariable = "visibility: visible;";
      } else if (this.openCloseVariable === "visibility: visible;") {
        this.openCloseVariable = "visibility: hidden;";
      }
    },
  },
};
</script>

<style>
/* CSSSSSSSSSSSS its just so stressfull :*/
@media only screen and (max-width: 500px) and (orientation: portrait) {
  .navbar-logo {
    height: 30px;
  }
  a {
    font-size: 15px !important;
    font-weight: bold;
  }
  .button_Header {
    font-size: 10px !important;
    text-align: center !important;
    height: 50px !important;
    box-shadow: none;
    transition: 0s !important;
    -webkit-transition: 0s !important;
  }
  .button_Header:hover {
    transform: translate(-4px, 5px);
    -webkit-transform: translate(-4px, 5px);
    transition: 0s !important;
    -webkit-transition: 0s !important;
    box-shadow: none;
  }
}
.header {
  background: white;
}
.navbar-logo {
  max-height: 50px;
}
a {
  margin: 0px;
  padding: 0px;
